@import "./variables.scss";


#modal-popup {
    position: fixed;
    top : 0;
    z-index : 3;
    display : flex;
    min-height: 100%;
    width : 100%;
    justify-content : center;
    background-color : #00000088;
    padding : 0rem 1rem;

    pointer-events: none;
    visibility: hidden;
    box-sizing: border-box;



    .modal-wrapper {
        position: relative;
        align-self : top;
        position: sticky;
        top : 2rem;
        height: 100%;
        width : 100%;
        max-width : 600px;
        display: block;
        padding : 1rem 0rem;
        padding-bottom : 0rem;
        border-radius : 1rem;
        background-color : white;
        background-color : #303030;
        box-shadow : 0px 3px 10px rgba(29, 29, 29, 0.664);
    
        .title-content {
            font-size : 1.5rem;
            border : none;
            margin-left : .7rem;
            color : white;
        }

        .close {
            position : absolute;
            right :1rem;
            top : .75rem;
            background-color: #303030;

            .close-icon {
                font-weight : 600;
                font-size : 1.4rem;
                color : white;
                background-color: #303030;
            }

            .close-icon:hover  {
                color: $base-color-pink;
            }



        }   

        .date-picker-wrapper {
            padding : 1rem 0rem;
            padding-bottom : 0rem;
            display : flex;
            flex-direction: column;
            position : relative;
            width : 100%;
            background-color : $base-color-pink;
            color : white;

            .date-picker {
                text-align : center;
                position : relative;
                width :  100%;
                
                

                .selected-full-date {
                    font-size : 1.2rem;
                    margin-bottom : .5rem;
                }


                .year {
                    position : relative;
                    font-size : 1.2rem;  
                    margin-bottom : .5rem;

                    .selected-year {
                        cursor : pointer;
                        padding : .5rem;
                    }

                    .selected-year:hover {
                        background-color : #c74280;
                    }

                    .year-list {
                        margin :  .75rem 0rem;
                        margin-top : 0rem;
                        background-color : $base-color-blue;
                        display : none;
                        width : 100%;
                        text-align : center;
                        overflow-y : scroll;
                        height : 110px;
                        border : 1px solid rgba(20, 20, 20, 0.616);

                        -ms-overflow-style: none;
                        scrollbar-width: none;

                        li  {
                            padding : 10px 0rem;
                            font-size : 15px;
                            width : 100%;
                            cursor :  pointer;
                        }

                        li:hover,
                        li.selected {
                            background-color : $base-color-dark-blue;
                        }

                        li:hover.selected {
                            background-color : $base-color-dark-pink;
                            color : #303030;
                        }
                    }

                    .year-list::-webkit-scrollbar {
                        display : none;
                    }
                
                    .year-list.active {
                        display : block;
                    }
                }

                .month {
                    display : flex;
                    justify-content : space-between;
                    font-size : 1.2rem;
                    margin-bottom : .5rem;

                    
                    
                    .inc-month,
                    .dec-month  {
                        display : flex;
                        justify-content : center;
                        align-items : center;
                        border-radius : 5px;
                        height: 30px;
                        width : 30px;
                        font-weight : 600;
                        color : white;
                        cursor : pointer;
                        margin : 0rem .75rem;
                    }

                    .inc-month:hover,
                    .dec-month:hover {
                        background-color : $base-color-blue;
                    } 
                
                }

                .days {
                    width :  100%;
                    display : grid;
                    grid-template-columns: repeat(7,1fr);

                    .day {
                        display : flex;
                        justify-content : center;
                        align-items : center;
                        padding : .75rem .5rem;
                        cursor : pointer;
                    }

                    .day.selected {
                        background-color : $base-color-blue;
                    }

                    .day:hover {
                        background-color : $base-color-blue;
                    }
                }

                .hour {
                    background-color : #303030;
                    width : 100%;
                    display : flex;

                    &-time,
                    &-part {
                        flex : 0 0 50%;
                        cursor : pointer;
                    }



                    &-time {
                        display: flex;
                        justify-content : space-between;
                        align-items : center;
    
                        .separator {
                            font-size : 1.7rem;
                            margin : 0rem .5rem
                        }
                        
                        input {                            width : 100%;
                            height : 100%;
                            border :  none;
                            outline : none;
                            background-color : #303030;
                            color : white;
                            font-weight : 600;
                            font-size : 1.2rem;
                            text-align : center;
                        }

                        input:hover {
                            background-color : rgb(31, 31, 31);
                        }
                    }

                    &-part {
                        display : flex;
                        cursor : default;
                        justify-content : space-around;
                        align-items: center;
                        text-align : center;
                        background-color: #252525;

                        h3 {
                            padding : .75rem 0rem;
                            cursor : pointer;
                            display: flex;
                            align-items : center;
                            justify-content : center;
                            height : 100%;
                            width : 100%;
                        }

                        h3:hover {
                            background-color : $base-color-dark-pink;
                        }
                        
                        h3.selected {
                            background-color : $base-color-dark-pink;
                        }
                    }
                }
            
            }
            
            .date-picker.active {
                display: block;
            }
        }

        .date-submit {
            display: block;
            width: 100%;
           

            button  {
                width: 100%;
                border : none;
                outline : none;
                background-color : $base-color-blue;
                padding : .5rem 1.2rem;
                font-weight : 600;
                color : white;
                border-radius : 0 0  15px 15px;
            }
        
            button:hover {
                background-color: $base-color-dark-blue;
            }
        
        }
    }
    
    .modal-wrapper.center {
        top : 10rem;
    }

    .modal-wrapper.bottom {
        top : 25rem;
    }


}

#modal-popup.active {
    pointer-events: visible;
    visibility: visible;
}